@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600&display=swap);
* {
    font-family: "Quicksand", sans-serif;
    margin: 0;
    --darkBlue: #1d3557;
    --powderBlue: #a8dadc;
    --celadonBlue: #457b9d;
    --imperialRed: #e63946;
    --honeydew: #f1faee;
    background-color: var(--honeydew);
    color: var(--darkBlue);
}

.navbar {
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom: solid 1px var(--powderBlue);
}

.navbar > h1 {
    color: var(--imperialRed);
}

.navbar .link {
    margin-left: auto;
}

.navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}

.navbar a:hover {
    color: var(--imperialRed);
}

#primary-butt {
    background-color: var(--imperialRed);
    border-radius: 10px;
    color: var(--honeydew);
}

.container {
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
    height: 100vh;
}

small {
    display: block;
}
/* Blogs */
.blog-preview {
    padding: 10px 16px;
    margin: 20px 0;
    border-bottom: 1px solid var(--powderBlue);
}

.blog-preview:hover {
    border-radius: 10px;
    box-shadow: 1px 3px 5px var(--imperialRed);
}

.blog-preview h2 {
    font-size: 20px;
    color: var(--celadonBlue);
    margin-bottom: 8px;
}

.blog-list a {
    text-decoration: none;
}

/* Blogs */

.blog-details h2 {
    font-size: 20px;
    color: var(--imperialRed);
    margin-bottom: 10px;
    text-shadow: 1px 3px 5px var(--celadonBlue);
}

.blog-details div {
    margin: 20px 0;
}

.blog-details button {
    background-color: var(--imperialRed);
    color: var(--honeydew);
    border: none;
    outline: none;
    padding: 8px;
    border-radius: 10px;
    cursor: pointer;
}

/* Create new blog form */

.create {
    max-width: 500px;
    margin: 0 auto;
    text-align: center;
}

.create h2 {
    font-size: 20px;
    color: var(--imperialRed);
    margin-bottom: 30px;
}

.create label {
    text-align: left;
    display: block;
    font-weight: bold;
}

.create input,
.create textarea,
.create select {
    width: 100%;
    padding: 6px 10px;
    resize: vertical;
    margin: 10px 0px;
    border: none;
    display: block;
    outline: 1px solid var(--powderBlue);
    box-sizing: border-box;
}

.create input:focus,
.create textarea:focus,
.create select:focus {
    border: 2px dashed var(--imperialRed);
    outline: none;
}
.create button {
    background-color: var(--celadonBlue);
    color: var(--honeydew);
    border: none;
    outline: none;
    padding: 8px;
    border-radius: 10px;
    cursor: pointer;
}

.create #adding-btn {
    background-color: var(--imperialRed);
}
/* Create new blog form */

/* NOT FOUND PAGE */
.not-found {
    max-width: 400px;
    text-align: center;
    margin: 0px auto;
}

.not-found h1 {
    color: var(--imperialRed);
    margin-bottom: 30px;
}

.not-found a {
    display: block;
    max-width: -webkit-max-content;
    max-width: max-content;
    margin: 90px auto 0px auto;
    text-decoration: none;
    background-color: var(--celadonBlue);
    border-radius: 10px;
    padding: 10px;
    color: var(--honeydew);
}
/* NOT FOUND PAGE */

